export var getFormattedPokemon = function (pokemon) {
    return {
        id: pokemon.id,
        name: pokemon.name,
        height: pokemon.height,
        weight: pokemon.weight,
        stats: {
            attack: pokemon.stats.find(function (item) { return item.stat.name === "attack"; })
                .base_stat,
            defense: pokemon.stats.find(function (item) { return item.stat.name === "defense"; })
                .base_stat,
            hp: pokemon.stats.find(function (item) { return item.stat.name === "hp"; }).base_stat,
        },
        imgDefaultUrl: pokemon.sprites.front_default,
        imgShinyUrl: pokemon.sprites.front_shiny,
        types: pokemon.types.map(function (item) { return item.type.name; }),
        abilities: pokemon.abilities.map(function (item) { return item.ability.name; }),
    };
};
